import React, { useState, useCallback } from "react";
import SelectedImage from "./SelectedImage";
import Gallery from "react-photo-gallery";
import Carousel, { Modal, ModalGateway } from "react-images";
import photoList from "./photoList";

function ReactPhotoGallery() {
  const [currentImage, setCurrentImage] = useState(0);
  const [viewerIsOpen, setViewerIsOpen] = useState(false);
  const [photos, setPhotos] = useState(photoList);
  const [selectAll, setSelectAll] = useState(false);
  const [enableSelection, setEnableSelection] = useState(false);

  const toggleSelectAll = () => {
    setSelectAll(!selectAll);
  };

  const openLightbox = useCallback((event, { photo, index }) => {
    setCurrentImage(index);
    setViewerIsOpen(true);
  }, []);

  const closeLightbox = () => {
    setCurrentImage(0);
    setViewerIsOpen(false);
  };

  const imageRenderer = useCallback(
    ({ index, left, top, key, photo }) => (
      <SelectedImage
        selected={selectAll ? true : false}
        key={key}
        margin={"2px"}
        index={index}
        photo={photo}
        left={left}
        top={top}
      />
    ),
    [selectAll]
  );

  return (
    <div>
      <p>
        {/* <button onClick={toggleSelectAll}>toggle select all</button> */}
        {!enableSelection && <button onClick={() => setEnableSelection(true)}>enable selection</button>}
        {enableSelection && <button onClick={() => setEnableSelection(false)}>done</button>}
      </p>
      <Gallery photos={photos} onClick={openLightbox} direction={'column'} renderImage={enableSelection ? imageRenderer : null}/>
      <ModalGateway>
        {viewerIsOpen ? (
          <Modal onClose={closeLightbox}>
            <Carousel
              currentIndex={currentImage}
              views={photos.map(x => ({
                ...x,
                srcset: x.srcSet,
                caption: x.title
              }))}
            />
          </Modal>
        ) : null}
      </ModalGateway>
    </div>
  );
}

export default ReactPhotoGallery;